.arena-bar-stats {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 500px;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  /* background:lightblue; */
  position: relative;
  background: rgba(0, 0, 0, 0.05);
}

.arena-bar-stats__list {
  list-style: none;
  height: 100%;
  padding: 0;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: relative;
}

.arena-bar-stats__item {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-weight: 600;
  font-size: 20px;
  height: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 10px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.arena-bar-stats__item:last-child {
  border-right: none;
}

.arena-bar-stats__item--label {
  font-weight: 600;
  opacity: 0.7;
}

.arena-bar-stats__item--value {
  opacity: 1;
}



